* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.app {
  width: 100%;
  min-height: 100vh;
  font-family: arial, sans-serif;
  background: url(/weather-application/static/media/bgc1.d47d96ac.jpg) no-repeat;
  background-size: cover;
  overflow: hidden;
}

.app__wrapper {
  position: relative;
  min-height: calc(100vh - 20px);
  width: 100%;
  margin: 0 auto;
}

.header {
  padding: 35px 20px;
  background-color: rgba(73, 73, 73, 0.6);
}

.header__wrapp {}

main {
  max-width: 500px;
  margin: 0 auto;
}

.information {
  position: relative;
}

.information__loader {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.form {
    display: -webkit-flex;
    display: flex;
    margin: 0 10px;
}

.form__input {
    color: #E0E0E0;
    background-color: #494949;
    border: 1px solid white;
    margin: 0 auto;
    font-size: 18px;
    letter-spacing: 1px;
    padding: 5px 10px 5px 10px;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border-style: none;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    outline: none;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    border: 1px solid #fff;
}

.form__input::-webkit-input-placeholder {
    color: #E0E0E0;
    -webkit-transition: .2s linear;
    transition: .2s linear;
}

.form__input::-ms-input-placeholder {
    color: #E0E0E0;
    -webkit-transition: .2s linear;
    transition: .2s linear;
}

.form__input::placeholder {
    color: #E0E0E0;
    -webkit-transition: .2s linear;
    transition: .2s linear;
}

.form__input:hover::-webkit-input-placeholder {
    color: #fff;
}

.form__input:hover::-ms-input-placeholder {
    color: #fff;
}

.form__input:hover::placeholder {
    color: #fff;
}

.form__input:focus {
    background-color: #696868;
    color: #fff;
}

.form__input:hover {
    background-color: #696868;
}

.form__button {
    padding: 0 10px 0 10px;
    font-size: 20px;
    color: #E0E0E0;
    border-style: none;
    border: 1px solid white;
    background-color: #494949;
    -webkit-transition: .2s linear;
    transition: .2s linear;

}

.form__button:hover {
    cursor: pointer;
    color: #fff;
    background-color: #696868;
}

@media (min-width:768px) {
    .form {
        padding: 0 10px;
        margin: 0 auto;
        max-width: 600px;
    }
}
.footer {
    position: relative;
    background-color: rgba(73, 73, 73, 0.6);
    color: #fff;
    text-align: center;
    height: 20px;
    font-size: 10px;
}

.footer__item {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.nav {}

.menu {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 30px auto 0;
    max-width: 500px;
}

.menu__item {
    list-style: none;
    width: 100%;
    margin: 0 10px 0 10px;
}

.menu__item--btn {
    display: block;
    padding: 10px 0;
    text-decoration: none;
    text-transform: uppercase;
    color: #eee;
    border: 1px solid white;
    letter-spacing: 2px;
    border-radius: 8px;
    text-align: center;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    max-width: 165px;
    margin: 0 auto;
}

.menu__item--btn:hover {
    background-color: #696868;
    color: #fff;
}


.menu__item--btnActive {
    background-color: #696868;
    color: #fff;
}
.result {
    position: relative;
    padding: 40px 80px 80px 80px;
}

.result__image {
    font-size: 100px;
    color: white;
    position: absolute;
    top: 40px;
    right: 55%;
}

.result__data {
    position: absolute;
    top: 40px;
    left: 55%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: #68CBD8;
    padding-right: 10px;
}

.result__data span:nth-child(1) {
    font-size: 36px;

}

.result__data span:nth-child(2) {
    font-size: 22px;
}

.result__data span:nth-child(3) {
    color: #fff;
}

.result__data span:nth-child(3)::first-letter {
    text-transform: uppercase;
}

.result__details {

    margin-top: 90px;
    color: #fff;
    position: relative;
}

.result__details--containerLeft {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    bottom: 0;
    left: 5%;
}

.result__details--containerLeft span:nth-child(1) {
    font-size: 24px;
}

.result__details--containerRight {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: absolute;
    text-align: right;
    bottom: 0;
    right: 5%;
}

.result__details--containerRight span:nth-child(1) {
    font-size: 24px;
}

.information__container {
    padding: 10px 0 20px;
    height: 10px;
}

.information__container--line {
    display: block;
    margin: 0 auto;
    width: 90%;
    border: 1px solid rgba(255, 255, 255, 0.7);
}

.result__particulars {
    color: #fff;
}

.particulars__container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 15px 0;
}

.particulars__wrapp {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 20px;
}

.particulars__wrapp--name {
    font-size: 14px;
    margin-bottom: 20px;
}

@media(min-width:360px) {
    .result {
        padding: 80px 0 80px 0;
    }

    .particulars__container {
        margin: 40px 0 0 0;
    }
}

@media(min-width:375px) and (min-height:812px) {
    .result {
        margin: 30px 0;
    }

    .result__image {
        font-size: 120px;
    }

    .result__data span:nth-child(1) {
        font-size: 44px;

    }

    .result__data span:nth-child(2) {
        font-size: 30px;
    }

    .result__details {

        margin-top: 140px;
    }

    .result__details--containerLeft span:nth-child(1) {
        font-size: 32px;
    }

    .result__details--containerLeft span:nth-child(2) {
        font-size: 18px;
    }

    .result__details--containerRight span:nth-child(1) {
        font-size: 32px;
    }

    .result__details--containerRight span:nth-child(2) {
        font-size: 18px;
    }


    .particulars__wrapp {
        font-size: 24px;
    }

    .particulars__wrapp--name {
        font-size: 18px;
    }


    .particulars__container {
        margin: 60px 0 0 0;
    }
}

@media(min-width:411px) {
    .result {
        margin: 10px 0;
    }

    .result__image {
        font-size: 120px;
    }

    .result__data span:nth-child(1) {
        font-size: 44px;

    }

    .result__data span:nth-child(2) {
        font-size: 30px;
    }

    .result__details {

        margin-top: 140px;
    }

    .result__details--containerLeft span:nth-child(1) {
        font-size: 32px;
    }

    .result__details--containerLeft span:nth-child(2) {
        font-size: 18px;
    }

    .result__details--containerRight span:nth-child(1) {
        font-size: 32px;
    }

    .result__details--containerRight span:nth-child(2) {
        font-size: 18px;
    }


    .particulars__wrapp {
        font-size: 24px;
    }

    .particulars__wrapp--name {
        font-size: 18px;
    }

}
.information__homePage {
    padding: 40px;
    color: #fff;
    text-align: center;
}

.homePage--title {
    font-size: 36px;
    margin-bottom: 20px;
}

.homePage--description {
    font-size: 20px;
}
.information__errorPage {
    padding: 40px;
    color: #fff;
    text-align: center;
}

.errorPage--title {
    font-size: 36px;
    margin-bottom: 20px;
}

